}
.header__item:hover {
  color: rgb(4, 217, 255);
  text-shadow: 2px 2px 3px #000;
}
.header__item:hover::before {
  width: 75%;
}
.header__item:hover::after {
  width: 40%;
}
.header__item.active {
  color: rgb(4, 217, 255);
  text-shadow: 2px 2px 3px #000;
}
.header__item.active::before {
  width: 75%;
}
.header__item.active::after {
  width: 40%;
}
