.header__ellipsis {
  position: relative;
  padding: 2rem;
  display: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 920.99px) {
  .header__ellipsis {
    display: none;
  }
}
.header__ellipsis span {
  top: 50%;
  margin-top: -3px;
  position: absolute;
  width: 6px;
  height: 6px;
  line-height: 6px;
  text-align: center;
  background: rgb(204, 255, 0);
  left: 50%;
  margin-left: -3px;
  border-radius: 50%;
  transition: 0.3s all ease;
}
.header__ellipsis span::before, .header__ellipsis span::after {
  content: "";
  position: absolute;
  width: 6px;
  height: 6px;
  line-height: 6px;
  text-align: center;
  background: rgb(204, 255, 0);
  left: 50%;
  margin-left: -3px;
  border-radius: 50%;
  transition: 0.4s all ease;
}
.header__ellipsis span::before {
  bottom: 100%;
  margin-bottom: 3px;
}
.header__ellipsis span::after {
  top: 100%;
  margin-top: 3px;
}
.header__ellipsis._active span {
  transform: scale(0.7);
}

@keyframes fadeOutGradient {
  100% {
    background-position: 100% 0;
  }
}
@keyframes fadeInGradient {
  100% {
    background-position: 100% 0;
  }
}
